___CSS_LOADER_EXPORT___.push([module.id, `.menu {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 0;
  list-style: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  margin-bottom: 0;
  padding-inline-start: 0;
  outline: none;
  transition: background 0.3s, width 0.3s cubic-bezier(0.2, 0, 0, 1) 0s;
  width: 100%;
  color: rgba(0, 0, 0, 0.88);
  background: #ffffff;
  box-shadow: none;
  border-inline-end: 0px solid rgba(5, 5, 5, 0.06);
}

.menu::before {
  display: table;
  content: "";
}
.menu-title-content {
  transition: color 0.3s;
}

.menu-item {
  height: 40px;
  line-height: 40px;
  border-radius: 8px;
  position: relative;
  display: block;
  margin: 0;
  white-space: nowrap;
  cursor: pointer;
  transition: border-color 0.3s, background 0.3s,
    padding 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);

  padding-left: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 4px;
  width: calc(100% - 8px);
}

.menu-item-selected {
  background-color: #e6f4ff;
  color: #1677ff;
}

.menu-item:not(.menu-item-selected):active {
  background-color: #e6f4ff;
}

.menu-item:not(.menu-item-selected):hover {
  background-color: rgba(0, 0, 0, 0.06);
}

.menu-item-title {
  margin-left: 12px;
}
