___CSS_LOADER_EXPORT___.push([module.id, `@keyframes spinMove {
  to {
    opacity: 1;
  }
}

@keyframes rotate {
  to {
    transform: rotate(405deg);
  }
}

.spin-nested-loading {
  position: relative;
}

.spin-nested-loading,
.spin-container {
  height: 100%;
  position: relative;
  transition: opacity 0.3s;
}

.spin {
  position: absolute;
  top: 0;
  inset-inline-start: 0;
  z-index: 4;
  display: block;
  width: 100%;
  height: 100%;
  max-height: 400px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #1677ff;
  font-size: 14px;
  line-height: 1.5714285714285714;
  list-style: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  position: absolute;
  display: none;
  text-align: center;
  vertical-align: middle;
  opacity: 0;
  transition: transform 0.3s cubic-bezier(0.78, 0.14, 0.15, 0.86);
}

.spin-spinning {
  /* position: static; */
  display: inline-block;
  opacity: 1;
}

.spin .spin-dot-item {
  position: absolute;
  display: block;
  width: 9px;
  height: 9px;
  background-color: #1677ff;
  border-radius: 100%;
  transform: scale(0.75);
  transform-origin: 50% 50%;
  opacity: 0.3;
  animation-name: spinMove;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-direction: alternate;
}

.spin-lg .spin-dot i {
  width: 14px;
  height: 14px;
}

.spin .spin-dot-item:nth-child(1) {
  top: 0;
  left: 0;
}

.spin .spin-dot-item:nth-child(2) {
  top: 0;
  right: 0;
  animation-delay: 0.4s;
}

.spin .spin-dot-item:nth-child(3) {
  right: 0;
  bottom: 0;
  animation-delay: 0.8s;
}

.spin .spin-dot-item:nth-child(4) {
  bottom: 0;
  left: 0;
  animation-delay: 1.2s;
}

.spin .spin-dot {
  position: relative;
  display: inline-block;
  font-size: 20px;
  width: 1em;
  height: 1em;
}

.spin .spin-dot-spin {
  transform: rotate(45deg);
  animation-name: rotate;
  animation-duration: 1.2s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.spin-lg .spin-dot {
  font-size: 32px;
}

.spin-nested-loading > div > .spin .spin-dot {
  position: absolute;
  top: 50%;
  inset-inline-start: 50%;
  margin: -10px;
}

.spin-nested-loading > div > .spin-lg .spin-dot {
  margin: -16px;
}

.spin-nested-loading .spin-blur {
  clear: both;
  opacity: 0.5;
  user-select: none;
  pointer-events: none;
}
